*{
  margin: 0;
  padding: 0;
}
.headerNavi{
  position: relative;
}
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
  }
  
  li {
    float: left;
  }
  
  li button {
    display: block;
    color: white;
    background-color: #333;
    text-align: center;
    padding: 10px 12px;
    text-decoration: none;
    border: none;
  }
  
  li button:hover {
    background-color: #111;
    cursor: pointer;
  }
html, body {
    min-height: 100%;
    height: 100%;
    background-image: url(http://theartmad.com/wp-content/uploads/Dark-Grey-Texture-Wallpaper-5.jpg);
    background-size: cover;
    background-position: top center;
    font-family: helvetica neue, helvetica, arial, sans-serif;
    font-weight: 200;
  }
  html.modal-active, body.modal-active {
    overflow: hidden;
  }
  .closeButton{
    position: fixed;
    top: 10%;
    right: 10px;
    padding: 1% 5%;
    color: white;
    background-color: #333;
    border-radius: 3px;
    opacity: 0.3;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    z-index: 1000;
  }
  .closeButton:hover{
    color: #333;
    background-color: white;
    opacity: 1;
  }
  .arrowRightButton{
    position: fixed;
    top: 40%;
    right: 10px;
    padding: 5% 1%;
    color: white;
    background-color: #333;
    border-radius: 3px;
    opacity: 0.3;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    z-index: 1000;
  }
  .arrowRightButton:hover{
    color: #333;
    background-color: white;
    opacity: 1;
  }
  .arrowLeftButton{
    position: fixed;
    top: 40%;
    left: 10px;
    padding: 5% 1%;
    color: white;
    background-color: #333;
    border-radius: 3px;
    opacity: 0.3;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    z-index: 1000;
  }
  .arrowLeftButton:hover{
    color: #333;
    background-color: white;
    opacity: 1;
  }
  #modal-container {
    position: fixed;
    display: table;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    z-index: 1;
  }
  #modal-container.modalWindow {
    -webkit-transform: scaleY(0.01) scaleX(0);
            transform: scaleY(0.01) scaleX(0);
    -webkit-animation: unfoldIn 1s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
            animation: unfoldIn 1s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  }
  #modal-container.modalWindow .modal-background .modal {
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-animation: zoomIn 0.5s 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
            animation: zoomIn 0.5s 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  }
  #modal-container.modalWindow.out {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-animation: unfoldOut 1s 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
            animation: unfoldOut 1s 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  }
  #modal-container.modalWindow.out .modal-background .modal {
    -webkit-animation: zoomOut 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
            animation: zoomOut 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  }
  #modal-container .modal-background {
    display: table-cell;
    background: rgba(0, 0, 0, .8);
    text-align: center;
    vertical-align: middle;
  }
  #modal-container .modal-background .modal {
    background: white;
    padding: 0px;
    display: inline-block;
    border-radius: 3px;
    font-weight: 300;
    position: relative;
    width: 100%;
    height: 600px;
    overflow-y: auto;
  }
  #modal-container .modal-background .modal h2 {
    font-size: 25px;
    line-height: 25px;
    margin-bottom: 15px;
  }
  #modal-container .modal-background .modal p {
    font-size: 18px;
    line-height: 22px;
  }
  @-webkit-keyframes unfoldIn {
    0% {
      -webkit-transform: scaleY(0.005) scaleX(0);
              transform: scaleY(0.005) scaleX(0);
    }
    50% {
      -webkit-transform: scaleY(0.005) scaleX(1);
              transform: scaleY(0.005) scaleX(1);
    }
    100% {
      -webkit-transform: scaleY(1) scaleX(1);
              transform: scaleY(1) scaleX(1);
    }
  }
  @keyframes unfoldIn {
    0% {
      -webkit-transform: scaleY(0.005) scaleX(0);
              transform: scaleY(0.005) scaleX(0);
    }
    50% {
      -webkit-transform: scaleY(0.005) scaleX(1);
              transform: scaleY(0.005) scaleX(1);
    }
    100% {
      -webkit-transform: scaleY(1) scaleX(1);
              transform: scaleY(1) scaleX(1);
    }
  }
  @-webkit-keyframes unfoldOut {
    0% {
      -webkit-transform: scaleY(1) scaleX(1);
              transform: scaleY(1) scaleX(1);
    }
    50% {
      -webkit-transform: scaleY(0.005) scaleX(1);
              transform: scaleY(0.005) scaleX(1);
    }
    100% {
      -webkit-transform: scaleY(0.005) scaleX(0);
              transform: scaleY(0.005) scaleX(0);
    }
  }
  @keyframes unfoldOut {
    0% {
      -webkit-transform: scaleY(1) scaleX(1);
              transform: scaleY(1) scaleX(1);
    }
    50% {
      -webkit-transform: scaleY(0.005) scaleX(1);
              transform: scaleY(0.005) scaleX(1);
    }
    100% {
      -webkit-transform: scaleY(0.005) scaleX(0);
              transform: scaleY(0.005) scaleX(0);
    }
  }
  @-webkit-keyframes zoomIn {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes zoomIn {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @-webkit-keyframes zoomOut {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @keyframes zoomOut {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @-webkit-keyframes fadeIn {
    0% {
      background: rgba(0, 0, 0, .0);
    }
    100% {
      background: rgba(0, 0, 0, .7);
    }
  }
  @keyframes fadeIn {
    0% {
      background: rgba(0, 0, 0, .0);
    }
    100% {
      background: rgba(0, 0, 0, .7);
    }
  }
  @-webkit-keyframes fadeOut {
    0% {
      background: rgba(0, 0, 0, .7);
    }
    100% {
      background: rgba(0, 0, 0, .0);
    }
  }
  @keyframes fadeOut {
    0% {
      background: rgba(0, 0, 0, .7);
    }
    100% {
      background: rgba(0, 0, 0, .0);
    }
  }
  @-webkit-keyframes scaleUp {
    0% {
      -webkit-transform: scale(0.8) translateY(1000px);
              transform: scale(0.8) translateY(1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(1) translateY(0px);
              transform: scale(1) translateY(0px);
      opacity: 1;
    }
  }
  @keyframes scaleUp {
    0% {
      -webkit-transform: scale(0.8) translateY(1000px);
              transform: scale(0.8) translateY(1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: scale(1) translateY(0px);
              transform: scale(1) translateY(0px);
      opacity: 1;
    }
  }
  @-webkit-keyframes scaleDown {
    0% {
      -webkit-transform: scale(1) translateY(0px);
              transform: scale(1) translateY(0px);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(0.8) translateY(1000px);
              transform: scale(0.8) translateY(1000px);
      opacity: 0;
    }
  }
  @keyframes scaleDown {
    0% {
      -webkit-transform: scale(1) translateY(0px);
              transform: scale(1) translateY(0px);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(0.8) translateY(1000px);
              transform: scale(0.8) translateY(1000px);
      opacity: 0;
    }
  }
  @-webkit-keyframes scaleBack {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0.85);
              transform: scale(0.85);
    }
  }
  @keyframes scaleBack {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0.85);
              transform: scale(0.85);
    }
  }
  @-webkit-keyframes scaleForward {
    0% {
      -webkit-transform: scale(0.85);
              transform: scale(0.85);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scaleForward {
    0% {
      -webkit-transform: scale(0.85);
              transform: scale(0.85);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @-webkit-keyframes quickScaleDown {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    99.9% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @keyframes quickScaleDown {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    99.9% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    100% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
  }
  @-webkit-keyframes sketchIn {
    0% {
      stroke-dashoffset: 778;
    }
    100% {
      stroke-dashoffset: 0;
    }
  }
  @keyframes sketchIn {
    0% {
      stroke-dashoffset: 778;
    }
    100% {
      stroke-dashoffset: 0;
    }
  }
  @-webkit-keyframes sketchOut {
    0% {
      stroke-dashoffset: 0;
    }
    100% {
      stroke-dashoffset: 778;
    }
  }
  @keyframes sketchOut {
    0% {
      stroke-dashoffset: 0;
    }
    100% {
      stroke-dashoffset: 778;
    }
  }
  @-webkit-keyframes modalFadeIn {
    0% {
      background-color: transparent;
    }
    100% {
      background-color: white;
    }
  }
  @keyframes modalFadeIn {
    0% {
      background-color: transparent;
    }
    100% {
      background-color: white;
    }
  }
  @-webkit-keyframes modalFadeOut {
    0% {
      background-color: white;
    }
    100% {
      background-color: transparent;
    }
  }
  @keyframes modalFadeOut {
    0% {
      background-color: white;
    }
    100% {
      background-color: transparent;
    }
  }
  @-webkit-keyframes modalContentFadeIn {
    0% {
      opacity: 0;
      top: -200px;
    }
    100% {
      opacity: 1;
      top: 0;
    }
  }
  @keyframes modalContentFadeIn {
    0% {
      opacity: 0;
      top: -200px;
    }
    100% {
      opacity: 1;
      top: 0;
    }
  }
  @-webkit-keyframes modalContentFadeOut {
    0% {
      opacity: 1;
      top: 0px;
    }
    100% {
      opacity: 0;
      top: -200px;
    }
  }
  @keyframes modalContentFadeOut {
    0% {
      opacity: 1;
      top: 0px;
    }
    100% {
      opacity: 0;
      top: -200px;
    }
  }
html,body { height: 90%; margin: 0px; padding: 0px; }
/* width */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(204, 204, 204); 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(122, 122, 122); 
  }
.imageGalleryItem{
    margin: 5%;
    width: 100%;
    position: absolute;
    top: 15%;
    right: 3%;
    -webkit-transform: scale(1.7) ;
            transform: scale(1.7) ;
    transition: .4s ease;
    
}
.imageGalleryItem:hover{
    -webkit-transform: scale(2.3);
            transform: scale(2.3);
    opacity: 0.3;
    top: 15%;
    right: 3%;
    cursor: pointer;
}
.centerDiv {
    background-color: #ffffff;
    width: 100%;
    height: 100%;
    overflow: auto;
    margin: 0 auto;
    margin-top: 5px;
    font-size: calc(10px + 2vmin);
    color: black;
}

